<script setup>
import { ref, onMounted, computed, watchEffect, nextTick, watch } from "vue";
import { ShadowRoot } from "vue-shadow-dom";
import { useRoute, useRouter } from "vue-router";
import { useBreadcrumbs } from "./src/useBreadcrumbs.js";

// Color scheme
import { setupColorScheme, useColorScheme } from "@/assets/js/functions/useColorScheme.js";
setupColorScheme();
const { prefersDark, setColorScheme } = useColorScheme();

// Store
import { useAppStore } from "@/store/app/app.js";
const appStore = useAppStore();

// Import comps
import Notifications from "@/components/utility/notifications/index.vue";
import AppIcon from "@/components/utility/icons/index.vue";
import LoadingIndicator from "@/components/utility/loading-indicator/index.ts";
import AppButton from "@/components/utility/app-button/index.vue";
import Modal from "@/components/utility/modal/index.vue";
import AppMenu from "@/components/app/app-menu/index.vue";

// Funcs
import { lmnFetch } from "@/assets/js/functions/lmnFetch.js";
import { isObject } from "@/assets/js/functions/isObject.js";
import { notify } from "@/assets/js/functions/notify.js";

// Refs
const adoptedStyleSheets = ref(new CSSStyleSheet());
const loading = ref(false);
const route = useRoute();
const settingsModal = ref(null);
const { breadcrumbs, refreshBreadcrumbs } = useBreadcrumbs();

/**
 * Injects styles into shadow root
 */
const setStyles = () => {
  let appStyleNode = document.querySelector("#snipvault-css");
  if (!appStyleNode) {
    appStyleNode = manuallyAddStyleSheet();
    // Wait for stylesheet to load
    appStyleNode.onload = () => {
      const appStyles = appStyleNode.sheet;
      for (const rule of [...appStyles.cssRules].reverse()) {
        adoptedStyleSheets.value.insertRule(rule.cssText);
      }
    };
  } else {
    const appStyles = appStyleNode.sheet;
    for (const rule of [...appStyles.cssRules].reverse()) {
      adoptedStyleSheets.value.insertRule(rule.cssText);
    }
  }

  //appStyleNode.remove();
};

const manuallyAddStyleSheet = () => {
  var link = document.createElement("link");
  link.rel = "stylesheet";
  link.href = `${appStore.state.siteURL}/wp-content/plugins/snipvault/app/dist/assets/styles/app.css`;
  document.head.appendChild(link);

  return link;
};

setStyles();
</script>

<template>
  <component is="style">#wpcontent{padding:0}#wpfooter{display:none}html{font-size:14px}</component>
  <ShadowRoot tag="div" :adopted-style-sheets="[adoptedStyleSheets]">
    <div
      class="snipvault-normalize bg-white dark:bg-zinc-900 h-screen max-h-screen w-screen text-zinc-500 fixed top-0 left-0 z-[99999] flex flex-col flex-nowrap text-zinc-700 dark:text-zinc-300 text-base"
      :class="!prefersDark ? '' : 'dark'"
    >
      <Notifications />
      <LoadingIndicator />

      <!--Toolbar-->
      <div class="flex flex-row">
        <AppMenu />
        <div class="flex flex-col grow max-h-screen overflow-hidden">
          <div class="flex flex-row items-center gap-2 border-b border-zinc-200 dark:border-zinc-700 p-3 px-4 place-content-between">
            <div class="flex flex-row gap-1 text-sm items-center text-zinc-500 dark:text-zinc-400">
              <RouterLink to="/" class="flex flex-row items-center gap-1 hover:text-zinc-900 dark:hover:text-zinc-100 transition-colors">
                <AppIcon icon="home" class="text-lg" />
                <span>{{ __("Home", "snipvault") }}</span>
              </RouterLink>

              <AppIcon icon="chevron_right" v-if="breadcrumbs.length" />

              <span v-for="(crumb, index) in breadcrumbs" :key="index" class="flex flex-row gap-1 items-center">
                <RouterLink :to="crumb.path" class="hover:text-zinc-900 dark:hover:text-zinc-100 transition-colors" :class="index == breadcrumbs.length - 1 ? 'text-zinc-900 dark:text-zinc-100' : ''">
                  <span v-html="crumb.name"></span>
                </RouterLink>
                <AppIcon icon="chevron_right" v-if="index < breadcrumbs.length - 1" />
              </span>
            </div>
          </div>

          <div class="flex flex-row flex-nowrap grow h-full max-h-full overflow-hidden">
            <RouterView id="mainroute" />
          </div>
          <Modal ref="settingsModal" position="top"><AppSettings :modal="settingsModal" /></Modal>
        </div>
      </div>
    </div>
  </ShadowRoot>
</template>

<style>
.snipvault-normalize * {
  box-sizing: border-box;
}
.darktoggle-enter-active,
.darktoggle-leave-active {
  transition: all 0.5s ease;
}
.darktoggle-enter-from,
.darktoggle-leave-to {
  opacity: 0;
  transform: translateX(30px);
}
</style>
