<script setup>
import { ref, computed, defineProps, useAttrs } from "vue";
import AppIcon from "@/components/utility/icons/index.vue";

const props = defineProps(["type", "loading", "buttontype", "icon"]);
const attrs = useAttrs();

const primary =
  "bg-zinc-800 px-3 py-1 text-zinc-200 hover:text-zinc-50 hover:bg-zinc-900 transition-all border border-zinc-900 disabled:text-zinc-600 outline outline-1 outline-offset-[-2px] outline-zinc-100/10 dark:outline-0 dark:border-zinc-700 dark:hover:bg-zinc-800";
const defaultclass =
  "px-3 py-1 bg-transparent border border-zinc-200 dark:border-zinc-700  hover:bg-zinc-50 text-zinc-600 dark:text-zinc-200 hover:text-zinc-800 dark:hover:text-zinc-100 shadow-sm dark:bg-transparent dark:hover:bg-zinc-800";
const dangerclass = "px-3 py-1 bg-red-50 dark:bg-red-700/20 dark:hover:bg-red-500/20 border border-red-200 dark:border-red-700 hover:border-red-300 hover:bg-red-100 text-red-600 shadow-sm";
const warningclass =
  "px-3 py-1 bg-orange-50 border border-orange-200 hover:border-orange-300 hover:bg-orange-100 text-orange-500 shadow-sm dark:bg-orange-700/40 dark:hover:bg-orange-700/60 dark:border-orange-700/60";
const transparent = "p-1 border-transparent hover:border-zinc-200 hover:bg-zinc-100 dark:hover:bg-zinc-800 text-zinc-500 hover:text-zinc-600  dark:text-zinc-300 dark:hover:text-zinc-100";

const hasLoader = computed(() => {
  return typeof props.loading !== "undefined";
});

const isLoading = computed(() => {
  return typeof props.loading === "function" ? props.loading() : props.loading;
});

const returnButtonStyles = computed(() => {
  if (props.type === "transparent") return transparent;
  if (props.type === "primary") return primary;
  if (props.type === "danger") return dangerclass;
  if (props.type === "warning") return warningclass;
  return defaultclass;
});
</script>

<template>
  <button :type="buttontype" class="transition-all rounded-lg w-auto cursor-pointer relative group whitespace-nowrap" :class="returnButtonStyles" v-bind="attrs">
    <div class="transition-opacity group-disabled:opacity-60 flex flex-row items-center gap-2" :class="isLoading ? 'opacity-0' : 'opacity-100'">
      <AppIcon v-if="icon" :icon="icon" />
      <slot />
    </div>
    <div v-if="hasLoader" class="absolute left-[50%] top-[50%] translate-y-[-50%] translate-x-[-50%] transition-opacity" :class="!isLoading ? 'opacity-0' : 'opacity-100'">
      <svg class="animate-spin h-[1.2rem] aspect-square" :class="type === 'primary' ? 'text-white' : 'text-zinc-400'" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
        <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
        <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
      </svg>
    </div>
  </button>
</template>
