export const setGlobalProperties = (appStore) => {
  // Get script tag
  const scriptTag = document.querySelector("#snipvault-data");

  // Bail if no script tag
  if (!scriptTag) return;

  // Get data attributes
  const pluginBase = scriptTag.getAttribute("plugin-base");
  const restBase = scriptTag.getAttribute("rest-base");
  const restNonce = scriptTag.getAttribute("rest-nonce");
  const adminUrl = scriptTag.getAttribute("admin-url");
  const loginUrl = scriptTag.getAttribute("login-url");
  const ajaxURL = scriptTag.getAttribute("ajax-url");
  const ajaxNonce = scriptTag.getAttribute("ajax-nonce");
  const siteURL = scriptTag.getAttribute("site-url");
  const userID = scriptTag.getAttribute("user-id");
  const appVersion = scriptTag.getAttribute("app-version");
  const signingKey = scriptTag.getAttribute("signing-key");

  // User roles
  let userRoles = scriptTag.getAttribute("user-roles");
  userRoles = userRoles ? JSON.parse(userRoles) : [];

  // Formatted Roles
  let formattedRoles = scriptTag.getAttribute("formatted-roles");
  formattedRoles = formattedRoles ? JSON.parse(formattedRoles) : [];

  // Formatted Post Types
  let postTypes = scriptTag.getAttribute("post_types");
  postTypes = postTypes ? JSON.parse(postTypes) : [];

  const userName = scriptTag.getAttribute("user-name");
  const userEmail = scriptTag.getAttribute("user-email");
  let frontPage = scriptTag.getAttribute("front-page");
  frontPage = frontPage == "true" ? true : false;

  let uipcSettings = scriptTag.getAttribute("snipvault-settings");
  uipcSettings = JSON.parse(uipcSettings);

  let canManageOptions = scriptTag.getAttribute("can-manage-options");
  canManageOptions = canManageOptions == "true" ? true : false;

  // Update store properties

  appStore.updateState("pluginBase", pluginBase);
  appStore.updateState("restBase", restBase);
  appStore.updateState("restNonce", restNonce);
  appStore.updateState("adminUrl", adminUrl);
  appStore.updateState("loginUrl", loginUrl);
  appStore.updateState("siteURL", siteURL);
  appStore.updateState("userID", userID);
  appStore.updateState("userRoles", userRoles);
  appStore.updateState("userName", userName);
  appStore.updateState("userEmail", userEmail);
  appStore.updateState("ajaxNonce", ajaxNonce);
  appStore.updateState("ajaxURL", ajaxURL);
  appStore.updateState("appVersion", appVersion);
  appStore.updateState("frontPage", frontPage);
  appStore.updateState("snipvault_settings", uipcSettings);
  appStore.updateState("canManageOptions", canManageOptions);
  appStore.updateState("signingKey", signingKey);
  appStore.updateState("formattedRoles", formattedRoles);
  appStore.updateState("postTypes", postTypes);
};
