<script setup>
import { ref, watch } from "vue";

// Store
import { useAppStore } from "@/store/app/app.js";
const appStore = useAppStore();

import AppIcon from "@/components/utility/icons/index.vue";
import AppButton from "@/components/utility/app-button/index.vue";

const menuItems = ref([
  { id: "snippets", icon: "code", label: __("Snippets", "snipvault"), link: "/", hover: false, visible: true },
  { id: "snippets-library", icon: "collections_bookmark", label: __("Library", "snipvault"), link: "/library", hover: false, visible: true },
  { id: "files", icon: "folder_open", label: __("File Browser", "snipvault"), link: "/file-browser", hover: false, visible: true },
  { id: "sql", icon: "database", label: __("SQL Editor", "snipvault"), link: "/sql-editor", hover: false, visible: true },
  { id: "cli", icon: "terminal", label: __("CLI Interface", "snipvault"), link: "/wp-cli", hover: false, visible: true },
  { id: "errorlog", icon: "data_object", label: __("PHP error log", "snipvault"), link: "/error-log", hover: false, visible: true },
  { id: "settings", icon: "tune", label: __("Settings", "snipvault"), link: "/settings", hover: false, visible: true },
]);

watch(
  () => appStore.state.snipvault_settings,
  () => {
    const fileBrowser = menuItems.value.find((item) => item.id == "files");
    const sqlEditor = menuItems.value.find((item) => item.id == "sql");
    const wpCli = menuItems.value.find((item) => item.id == "cli");

    fileBrowser.visible = !appStore.state.snipvault_settings.file_browsing_disabled;
    sqlEditor.visible = !appStore.state.snipvault_settings.sql_editor_disabled;
    wpCli.visible = !appStore.state.snipvault_settings.wp_sql_disabled;
  },
  { deep: true, immediate: true }
);
</script>

<template>
  <div class="border-r border-zinc-200 dark:border-zinc-700 h-screen flex flex-col">
    <div class="p-4 py-2 border-b border-zinc-200 dark:border-zinc-700 flex flex-col justify-center" style="height: 39.5px">
      <!--Logo-->
      <RouterLink to="/">
        <AppButton type="transparent">
          <AppIcon icon="snipvault-logo" class="text-zinc-900 dark:text-zinc-100" />
        </AppButton>
      </RouterLink>
    </div>

    <div class="flex flex-col gap-2 p-4 grow">
      <template v-for="item in menuItems">
        <RouterLink
          v-if="item.visible"
          :to="item.link"
          @mouseenter="item.hover = true"
          @mouseleave="item.hover = false"
          class="transition-all rounded-lg w-auto cursor-pointer relative group whitespace-nowrap p-1 border-transparent hover:border-zinc-200 hover:bg-zinc-100 dark:hover:bg-zinc-800 text-zinc-500 hover:text-zinc-600 dark:text-zinc-400 dark:hover:text-zinc-100"
          activeClass="bg-zinc-100 dark:bg-zinc-800 text-zinc-900 dark:text-zinc-100 relative [&[aria-current='page']]:text-zinc-900 [&[aria-current='page']]:dark:text-white"
        >
          <AppIcon :icon="item.icon" class="text-xl" />

          <Transition>
            <div class="absolute left-full top-1/2 -translate-y-1/2 z-[1]" v-if="item.hover">
              <div class="bg-white dark:bg-zinc-900 border border-zinc-200 dark:border-zinc-700 rounded-lg shadow px-2 py-1 text-sm">
                {{ item.label }}
              </div>
            </div>
          </Transition>
        </RouterLink>
      </template>
    </div>

    <div class="p-4 py-3 border-t border-zinc-200 dark:border-zinc-700 flex flex-col justify-center">
      <a :href="appStore.state.adminUrl" class="flex" :title="__('Back to dashboard', 'snipvault')">
        <AppButton type="transparent"><AppIcon icon="chevron_left" class="text-xl" /></AppButton>
      </a>
    </div>
  </div>
</template>
