<script setup>
import { ref } from "vue";

// Get noti store
import { useNotificationStore } from "@/store/notifications/notifications.js";
const notificationsStore = useNotificationStore();

// Import icons
import AppIcon from "@/components/utility/icons/index.vue";

/**
 * Returns bg color of icon
 */
const returnTickClass = (type) => {
  if (type === "success") return "bg-green-300/20 text-green-300 border-green-300/40";
  if (type === "warning") return "bg-orange-300/20 text-orange-300 border-orange-300/40";
  if (type === "error") return "bg-red-300/20 text-red-400 border-red-300/40";
  return "bg-indigo-300/20 text-indigo-300 border-indigo-300/40";
};
/**
 * Returns bg color of icon
 */
const returnIcon = (type) => {
  if (type === "success") return "tick";
  if (type === "warning") return "warning";
  if (type === "error") return "error";
  return "warning";
};
</script>

<style scoped>
.notiitem-enter-active,
.notiitem-leave-active {
  transition: all 0.3s ease-in-out;
}
.notiitem-enter-from,
.notiitem-leave-to {
  opacity: 0;
  transform: translateX(30px);
}
</style>

<template>
  <TransitionGroup tag="div" name="notiitem" class="fixed top-0 right-0 w-80 flex flex-col z-[999999] pointer-events-none">
    <div
      v-for="(noti, index) in notificationsStore.getAll"
      class="flex flex-row gap-4 items-center w-auto bg-zinc-950/80 text-zinc-100 shadow-lg rounded-2xl border border-zinc-800 p-3 mt-4 mr-4 backdrop-blur-md pointer-events-auto"
      :key="noti.id"
    >
      <div class="transition-all hover:bg-zinc-300 rounded-xl p-1 aspect-square border w-auto" :class="returnTickClass(noti.type)"><AppIcon :icon="returnIcon(noti.type)" /></div>
      <div class="flex flex-col flex-grow">
        <div class="text-sm" v-html="noti.title"></div>
        <div class="text-zinc-400 text-xs" v-html="noti.message"></div>
      </div>
      <!-- Loader-->
      <div v-if="noti.loader" class="transition-all hover:bg-zinc-100 rounded-xl p-2 aspect-square w-auto text-zinc-400 hover:text-zinc-600 cursor-pointer">
        <svg class="animate-spin h-5 w-5 text-indigo-600" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
          <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
          <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
        </svg>
      </div>
      <!-- Close-->
      <div
        v-if="noti.dismissable && !noti.loader"
        @click="notificationsStore.remove(index)"
        class="transition-all hover:bg-zinc-700 rounded-xl p-2 aspect-square w-auto text-zinc-400 hover:text-zinc-100 cursor-pointer"
      >
        <AppIcon icon="close" />
      </div>
    </div>
  </TransitionGroup>

  <component is="style">
    .notiitem-enter-active, .notiitem-leave-active { transition: all 0.3s ease-in-out; } .notiitem-enter-from, .notiitem-leave-to { opacity: 0; transform: translateX(30px); }
  </component>
</template>
