import { useRoute, useRouter } from "vue-router";
import { computed, ref, watch } from "vue";

export function useBreadcrumbs() {
  const route = useRoute();
  const router = useRouter();

  // Create a reactive trigger that we can update when meta changes
  const metaUpdateTrigger = ref(0);

  // Watch for route changes to update our trigger
  watch(
    () => route.meta,
    () => {
      metaUpdateTrigger.value++;
    },
    { deep: true }
  );

  // Method to force an update when meta changes without route change
  const refreshBreadcrumbs = () => {
    metaUpdateTrigger.value++;
  };

  const breadcrumbs = computed(() => {
    // Include the trigger in the computed dependency to force updates
    const _ = metaUpdateTrigger.value;

    const matched = route.matched;
    const result = [];

    // Track the path parts we've already processed
    let processedPath = "";

    matched.forEach((match, index) => {
      // For each matched route, we only want to add the new portion to our path
      let currentRoutePath = match.path;

      // If this is a nested route, make sure we're only adding the new part
      if (index > 0) {
        // Get the parent path
        const parentPath = matched[index - 1].path;

        // If the current path starts with the parent path, remove the parent path
        if (currentRoutePath.startsWith(parentPath)) {
          currentRoutePath = currentRoutePath.substring(parentPath.length);
        }

        // Remove leading slash if present
        if (currentRoutePath.startsWith("/")) {
          currentRoutePath = currentRoutePath.substring(1);
        }
      }

      // Add the new path portion
      processedPath = processedPath + (processedPath && currentRoutePath ? "/" : "") + currentRoutePath;

      // Replace parameters with actual values
      let resolvedPath = processedPath;
      Object.entries(route.params).forEach(([key, value]) => {
        // Handle regex route patterns like :filepath(.*)
        const paramRegex = new RegExp(`:${key}(\\(.*\\))?`, "g");
        resolvedPath = resolvedPath.replace(paramRegex, value);
      });

      // Ensure the path starts with a slash
      if (!resolvedPath.startsWith("/")) {
        resolvedPath = "/" + resolvedPath;
      }

      // Get name from meta
      let name;
      if (typeof match.meta.name === "function") {
        name = match.meta.name(route);
      } else {
        name = match.meta.name || match.name || "Unnamed";
      }

      result.push({
        name,
        path: resolvedPath,
        route: match,
      });
    });

    return result;
  });

  return {
    breadcrumbs,
    refreshBreadcrumbs,
  };
}
