import { createApp } from "vue";
import { createPinia } from "pinia";
import "./index.css";

// Import store
import { useAppStore } from "@/store/app/app.js";
import { setGlobalProperties } from "@/setup/setGlobalProperties.js";

// Import comps
import AppWrapper from "@/components/app/base/index.vue";
import { router } from "./setup/router.js";

// Build app
const app = createApp(AppWrapper);

// Use pinia
const pinia = createPinia();
app.use(router);
app.use(pinia);

// Update app store
const appStore = useAppStore();
setGlobalProperties(appStore);

// Declare translation functions
app.config.globalProperties.__ = __;
app.config.globalProperties.sprintf = sprintf;

const mountPoint = document.querySelector("#snipvault-app");
app.mount(mountPoint);
