import { createWebHashHistory, createRouter } from "vue-router";

import { unsavedChanges } from "./constants.js";

// Setup router
const routes = [
  {
    path: "/",
    component: () => import("@/components/app/framework/index.vue"),
    meta: { name: __("Snippets", "snipvault") },
    children: [
      {
        name: "snippet-inspect",
        path: "snippets/:id",
        component: () => import("@/components/app/snippet-editor/index.vue"),
        meta: { name: (route) => `#${route.params.id}` },
        children: [
          {
            name: "error-inspect",
            path: "errors/:errorindex",
            component: () => import("@/components/app/snippet-editor/src/error-inspect.vue"),
            meta: { name: (route) => `#${route.params.errorindex}` },
          },
        ],
      },
    ],
  },
  {
    path: "/library",
    component: () => import("@/components/app/library/index.vue"),
    meta: { name: __("Library", "snipvault") },
    children: [
      {
        name: "library-inspect",
        path: ":id",
        component: () => import("@/components/app/library-snippet-inspect/index.vue"),
        meta: { name: (route) => `#${route.params.id}` },
      },
    ],
  },
  {
    path: "/file-browser",
    component: () => import("@/components/app/file-browser/index.vue"),
    meta: { name: __("File Browser", "snipvault") },
    children: [
      {
        name: "file-editor",
        path: ":filepath(.*)", // Use regex to match any character, including slashes
        component: () => import("@/components/app/file-browser-editor/index.vue"),
        meta: { name: (route) => `${route.params.filepath}` },
      },
    ],
  },
  {
    path: "/wp-cli",
    component: () => import("@/components/app/cli-interface/index.vue"),
    meta: { name: __("WP CLI", "snipvault") },
    children: [],
  },
  {
    path: "/sql-editor",
    component: () => import("@/components/app/sql-editor/index.vue"),
    meta: { name: __("SQL editor", "snipvault") },
    children: [],
  },
  {
    path: "/error-log",
    component: () => import("@/components/app/error-log/index.vue"),
    meta: { name: __("PHP error log", "snipvault") },
    children: [],
  },
  {
    path: "/settings",
    component: () => import("@/components/app/settings/index.vue"),
    meta: { name: __("Settings", "snipvault") },
    children: [],
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

// Global navigation guard
router.beforeEach((to, from, next) => {
  // Check if there are unsaved changes in your application
  if (unsavedChanges.value) {
    // Show confirmation dialog
    const confirmed = window.confirm(__("You have unsaved changes. Are you sure you want to leave?", "snipvault"));

    if (confirmed) {
      // User confirmed, continue navigation
      unsavedChanges.value = false;
      next();
    } else {
      // User canceled, stay on current page
      next(false);
    }
  } else {
    // No unsaved changes, proceed normally
    next();
  }
});

export { router };
